import React, {Component} from 'react';
import {connect} from 'react-redux';

import QuantityInput from "./quantity/quantityInput";
import PlayersDetails from "./playersDetails/PlayersDetails";
import {initializePlayers} from "../store/actionCreator";


class Players extends Component {
	state = {
		numberOfPlayers: null
	};

	selectedPlayers = (players) => {
		this.setState({numberOfPlayers: players})
	}

	onPlayerInitializes = (playersInfo) => {
		this.props.initializedPlayers(playersInfo)
	}

	render() {	
		return this.state.numberOfPlayers ? <PlayersDetails players={this.state.numberOfPlayers} onDone={this.onPlayerInitializes} /> : <QuantityInput selectedPlayers={this.selectedPlayers} />
	}
}

const mapDispatchToState = (dispatch) => {
	return {
		initializedPlayers: playersInfo => dispatch(initializePlayers(playersInfo))
	}
}

export default connect(null, mapDispatchToState)(Players);
