import React from "react";
import {connect} from 'react-redux';

import "./Winner.css";

const winner = props => {
	return (
		<React.Fragment>
			<div className="congrats">Congratulations!</div>
			<div className="congrats-player-name" style={{color: props.winner.theme}}>{props.winner.name}</div>
		</React.Fragment>
	)
}

const mapStateToProps = state => {
	return {
		winner: state.winner
	}
}

export default connect(mapStateToProps)(winner);