import React, {Component} from 'react';
import "./Inputplayer.css"

class Inputplayer extends Component {
	state = {
		value: this.props.value,
		disable: this.props.disable
	}

	static getDerivedStateFromProps(props, state) {
		if(props.disable){
			state.disable = true;
		}
		return state;
	}

	onSubmitHandler = () => {
		this.props.onDone(this.state.value);
	}

	onChangeHandler = (event) => {
		this.setState({value: event.target.value});
	}

	render() {
		return (
			<div className={"single-input-container " + this.props.theme}>
				<input type="text" className={this.props.theme} placeholder={this.props.placeholder} value={this.state.value} onChange={this.onChangeHandler} disabled={this.state.disable}/>
				{
					this.state.disable ? null :
					<button  className={this.props.theme} onClick={this.onSubmitHandler}>done</button>
				}
			</div>

		)
	}
}

export default Inputplayer;