export const STEP_AHEAD = "STEP_AHEAD";
export const SET_DICE = "SET_DICE";
export const MOVE_GOTI = "MOVE_GOTI";
export const MOVE_GOTI_ANIMATION = "MOVE_GOTI_ANIMATION";
export const ADD_PLAYER = "ADD_PLAYER";
export const LEAVE_PLAYER = "LEAVE_PLAYER";
export const START_GAME = "START_GAME";
export const CHANGE_TURN = "CHANGE_TURN";
export const PLAYERS_INITIALIZE = "PLAYERS_INITIALIZE";

export const initializePlayers = (players) => {
	return {
		type: PLAYERS_INITIALIZE,
		players
	}
}

export const changeTurn = _ => {
	return {
		type: CHANGE_TURN
	}
}

export const stepAhead = (payload) => {
	return {
		type: STEP_AHEAD,
		payload
	}
}

export const setDice = (dice) => {
	return {
		type: SET_DICE,
		dice
	}
}

export const moveGoti = (position) => {
	return {
		type: MOVE_GOTI,
		position
	}
}

export const moveGotiAnimation = (position) => {
	return {
		type: MOVE_GOTI_ANIMATION,
		position
	}
}