import React, {Component} from 'react';
import './Board.css';
import {connect} from 'react-redux';

import snake from "../assets/images/snake.png";
import ladder from "../assets/images/ladder.png";
import {stepAhead, moveGoti, moveGotiAnimation, changeTurn} from "../store/actionCreator";
// import {sendEventForRefreshGoti,  subscribeToRefreshGoti} from "../api";

class Board extends Component {
	constructor(props) {
		super(props);
		this.state = {
			snakeMap: {
				98: 36,
				94: 69,
				76: 40,
				35: 18,
				55: 10
			},
			ladderMap: {
				6: 25,
				14: 48,
				21: 81,
				56: 77,
				67: 88
			},
			blockDice: false
		};

		//todo add here for sockets
		// subscribeToRefreshGoti(({diceValue, goti, socketId}, currentSocketId) => {
		// 	this.props.stepAhead(diceValue);
		// 	this.setState({blockDice: true});
		// 	setTimeout(_=>{
		// 		if(diceValue !== 6){
		// 			this.props.changeTurn();
		// 		}
		// 		this.setState({blockDice: false});
		// 	}, 1200)
		// })
	}
	


	componentDidUpdate() {
		let gotiInfo = this.props[this.props.currentTurn];
		if(gotiInfo.gotiPos <  gotiInfo.position){
			var diff = gotiInfo.position - gotiInfo.gotiPos;
			for(let i = 1; i<= diff; i++) {
				setTimeout(_=> {
					this.props.moveGotiAnimation(gotiInfo.gotiPos + i);
				}, 100 * i);	
			}
		}
		this.checkForSnake();	
		this.checkForLadder();	
	}

	checkForSnake() {
		let goti = this.props.currentTurn;
		let position = this.props[goti].position;
		if(this.state.snakeMap[position]) {
			setTimeout(_=>{
				this.props.moveGoti(this.state.snakeMap[position]);
			}, 500)
		}
	}

	checkForLadder() {
		let goti = this.props.currentTurn;
		let position = this.props[goti].position;
		if(this.state.ladderMap[position]) {
			setTimeout(_=>{
				this.props.moveGoti(this.state.ladderMap[position]);
			}, 500)
		}
	}

	clickHandler(goti) {
		let diceValue = Math.ceil(Math.random() * 6);
		
		//todo add here for sockets
		// sendEventForRefreshGoti({
		// 	diceValue,
		// 	goti: this.props.currentTurn
		// })


		this.props.stepAhead(diceValue);
		this.setState({blockDice: true});
		setTimeout(_=>{
			if(diceValue !== 6){
				this.props.changeTurn();
			}
			this.setState({blockDice: false});
		}, 1200)
	}	

	render() {
		//board initialization.
		const board = [...new Array(100)].map((elem, index) => {
			var mainIndex = 100 - index;
			if(mainIndex > 0 && mainIndex < 11) {
				mainIndex = 11 - mainIndex;
			} else if(mainIndex > 20 && mainIndex < 31) {
				mainIndex = 51 - mainIndex;
			} else if(mainIndex > 40 && mainIndex < 51) {
				mainIndex = 91 - mainIndex;
			} else if(mainIndex > 60 && mainIndex < 71) {
				mainIndex = 131 - mainIndex;
			} else if(mainIndex > 80 && mainIndex < 91) {
				mainIndex = 171 - mainIndex;
			}
			return <div className="board-cell" key={index}>
				{ mainIndex }
			</div>
		})

		// console.log("this.props.goties>>>",this.props)
		// player goties
		const goties = this.props.goties.map(goti => {
			var classNames = ["goti", goti];
			if(this.props[goti] && !this.props[goti].started) {
				classNames.push("not-started")
			}
			return <div className={classNames.join(" ")} style={{bottom: this.props[goti].posY, left: this.props[goti].posX}} key={goti}></div>
		})

		// snakes 
		const snakes = (
			<React.Fragment> 
				<img src={snake} className="snake1" alt=""/>
				<img src={snake} className="snake2" alt=""/>
				<img src={snake} className="snake3" alt=""/>
				<img src={snake} className="snake4" alt=""/>
				<img src={snake} className="snake5" alt=""/>
			</React.Fragment>
		);

		// ladders 
		const ladders = (
			<React.Fragment> 
				<img src={ladder} className="ladder1" alt=""/>
				<img src={ladder} className="ladder2" alt=""/>
				<img src={ladder} className="ladder3" alt=""/>
				<img src={ladder} className="ladder4" alt=""/>
				<img src={ladder} className="ladder5" alt=""/>
			</React.Fragment>
		);

		return (
			<div>
				<div className="ludo-container">
					<div className="board-container">
						{board}
						{goties}
						{snakes}
						{ladders}
					</div>
				</div>

				<div className={"dice-container " + this.props.currentTurn}>
					<div className="goti-preview"></div>
					<div className="player-name">
						{this.props[this.props.currentTurn].playerInfo.name}
					</div>

					{
						this.state.blockDice ?
							<div className="dice disable"> 
								{this.props.dice}
							</div>
						:
						<div onClick={ e => this.clickHandler('red')} className="dice"> 
							{this.props.dice}
						</div>
					}
				</div>
			</div>
		)
	}
}

/*<div className="dice"> 
								<div className="lds-hourglass"></div>
							</div>*/

const mapPropsToState = (state) => {
	return {
		...state
	}
}

const mapDispatchToState = (dispatch) => {
	return {
		stepAhead: (diceValue) => dispatch(stepAhead({diceValue})),
		moveGoti: (position) => dispatch(moveGoti(position)),
		moveGotiAnimation: (position) => dispatch(moveGotiAnimation(position)),
		changeTurn: _=> dispatch(changeTurn())
	}
}


export default connect(mapPropsToState, mapDispatchToState)(Board);