import React, {Component} from 'react';
import "./PlayersDetails.css";

import Inputplayer from "../../hoc/Inputplayer"

class Playersdetails extends Component {
	constructor(props) {
		super(props);
		this.state = {
			playersInfo: [...new Array(props.players)].map((i,index) => ({
				name: "player " + (index + 1),
				goti: ["red", "blue", "green", "aqua"][index],
				userEdited: false
			})),
			counter: 0
		}
	}

	profileCompleted(index, name) {
		let pInfo = {
			...this.state.playersInfo[index],
			name: name,
			userEdited: true
		};
		var newPlayersInfo = [...this.state.playersInfo];
		newPlayersInfo.splice(index, 1, pInfo);
		var counter = this.state.counter + 1;
		this.setState({playersInfo: newPlayersInfo, counter});
		if(counter === this.props.players) {
			this.props.onDone(newPlayersInfo);
		}
	}


	render() {
		var form = [...new Array(this.props.players)].map((i, index) => {
			return (
				<Inputplayer 
					key={index}
					placeholder={"player " + (index + 1) + " name"}
					value={this.state.playersInfo[index].name}
					onDone={name => this.profileCompleted(index, name)}
					disable={this.state.playersInfo[index].userEdited}
					theme={this.state.playersInfo[index].goti}
				/>
			);
		})

		return (
			<div className="formContaner"> 
				{form}
			</div>
		);
		
	}
}

export default Playersdetails;