import React from 'react';
import "./quantityInput.css"

const quantity = (props) => {
	return (
		<div className="quantity-main-container">
			<div className="quantity-option" onClick={e => props.selectedPlayers(1)}> Single Player </div>
			<div className="quantity-option" onClick={e => props.selectedPlayers(2)}> Two Player </div>
			<div className="quantity-option" onClick={e => props.selectedPlayers(3)}> Three Player </div>
			<div className="quantity-option" onClick={e => props.selectedPlayers(4)}> Four Player </div>
		</div>
	)
}

export default quantity