import {STEP_AHEAD, SET_DICE, MOVE_GOTI, MOVE_GOTI_ANIMATION, PLAYERS_INITIALIZE, CHANGE_TURN} from "./actionCreator";

const initialiState = {
	goties: [],
	playerInitialized: false,
	currentTurn: null,
	dice: 0,
	gameStarted: false,
	gameFinished: false,
	winner: null
};

const diceValueHandler = (value) => {
	let mainIndex = value;

	if(mainIndex > 10 && mainIndex < 21) {
		mainIndex = 21 - mainIndex;
	} else if(mainIndex > 30 && mainIndex < 41) {
		mainIndex = 41 - mainIndex;
	} else if(mainIndex > 50 && mainIndex < 61) {
		mainIndex = 61 - mainIndex;
	} else if(mainIndex > 70 && mainIndex < 81) {
		mainIndex = 81 - mainIndex;
	} else if(mainIndex > 90 && mainIndex < 101) {
		mainIndex = 101 - mainIndex;
	}

	let x = mainIndex % 10;
	if(x === 0){
		x = 10;
	}

	let y = parseInt(value / 10);
	let perX = ((x - 1) * 10) + '%';
	let perY = (( (value % 10 === 0) ? y - 1 : y) * 10) + '%';

	// document.documentElement.style.setProperty('--red-position-x', perX);
	// document.documentElement.style.setProperty('--red-position-y', perY);
	return {
		posX: perX,
		posY: perY
	};
}

const onGotiPositionChange = (state, diceValue) => {
	let goti = state.currentTurn;
	let gotiInfo = state[goti];
	let oldValue = gotiInfo.position;
	let newValue = oldValue + diceValue;

	if(!gotiInfo.started) {
		if(diceValue !== 6) {
			return {
				...state,
				dice: diceValue
			};
		} else {
			return {
				...state,
				[goti]: {
					...gotiInfo, 
					started: true, 
					lastNumber: diceValue,
					posX: 0,
					posY: 0
				},
				dice: diceValue
			};
		}
	}

	// can't go further..
	if(newValue > 100) {
		return {
			...state,
			dice: diceValue
		};;
	}


	let {posX, posY} = diceValueHandler(oldValue+1);

	let newState = {
		...state,
		dice: diceValue,
		[goti]: {
			...gotiInfo,
			position: newValue,
			gotiPos: oldValue +1,
			posX,
			posY
		}
	}

	if(newValue === 100) {
		let gotiIndex = newState.goties.indexOf(goti);
		newState.goties = [...newState.goties.splice(gotiIndex, 1)];

		newState[goti].finished = true;
		newState.gameFinished = true;
		newState.winner = {
			name: state[goti].playerInfo.name,
			theme: goti
		};
	}
	return newState;
}


const moveGotiPosition = (state, newValue) => {
	let goti = state.currentTurn;
	let gotiInfo = state[goti];

	let {posX, posY} = diceValueHandler(newValue);

	let newState = {
		...state,
		[goti]: {
			...gotiInfo,
			position: newValue,
			gotiPos: newValue,
			posX,
			posY
		}
	}
	return newState;
}

const moveGotiAnimation = (state, newValue) => {
	let goti = state.currentTurn;
	let gotiInfo = state[goti];

	let {posX, posY} = diceValueHandler(newValue);

	let newState = {
		...state,
		[goti]: {
			...gotiInfo,
			gotiPos: newValue,
			posX,
			posY
		}
	}
	return newState;
}

const initializePlayers = (state, players) => {
	let playerInfo = {};
	let goties = [];
	players.forEach(player => {
		playerInfo[player.goti] = {
			started: false,
			finished: false,
			lastNumber: 0,
			position: 1, 
			playerInfo: {
				name: player.name
			}
		}
		goties.push(player.goti);
	});

	return {
		...state,
		...playerInfo,
		goties,
		playerInitialized: true,
		currentTurn: goties[0],
		gameStarted: true
	}
}

const changeTurn = state => {
	if(state.gameFinished) {
		return {...state};
	}
	let oldTurnIndex = state.goties.indexOf(state.currentTurn);
	let newIndex = oldTurnIndex + 1;
	if(newIndex ===  state.goties.length) {
		newIndex = 0;
	}
	return {
		...state,
		currentTurn: state.goties[newIndex]
	}
}


const reducer = (state = initialiState, action) => {
	switch(action.type) {
		case PLAYERS_INITIALIZE: 
			return initializePlayers(state, action.players);
		case CHANGE_TURN:
			return changeTurn(state);
		case STEP_AHEAD:
			return onGotiPositionChange(state, action.payload.diceValue);
		case SET_DICE:
			return {
				...state,
				dice: action.dice
			}
		case MOVE_GOTI:
			return moveGotiPosition(state, action.position);
		case MOVE_GOTI_ANIMATION:
			return moveGotiAnimation(state, action.position);
		default:
			return state;
	}
}


export default reducer;